.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #f5f5f5;
}

.config-status {
  background: #e3f2fd;
  border: 1px solid #2196f3;
  border-radius: 4px;
  padding: 12px;
  margin: 16px;
  font-size: 12px;
  text-align: left;
}

.config-status h3 {
  margin: 0 0 8px 0;
  color: #1976d2;
  font-size: 14px;
}

.config-status ul {
  margin: 0;
  padding-left: 16px;
}

.config-status li {
  margin: 4px 0;
  color: #424242;
}

.error-banner {
  background: #ffebee;
  border: 1px solid #f44336;
  color: #c62828;
  padding: 12px 16px;
  margin: 16px;
  border-radius: 4px;
  font-size: 14px;
  text-align: center;
}